
import Vue from "vue"

export default Vue.extend({
  props: {
    title: {
      type: String,
      required: true,
      default: "",
    },
    url: {
      type: String,
      required: false,
      default: null,
    },
    date: {
      type: String,
      required: false,
      default: "2020",
    },
    position: {
      type: String,
      required: false,
      default: null,
    },
  },
  data() {
    return {
      hasBackground: false,
    }
  },
  methods: {
    toggleBackground() {
      this.hasBackground = true

      setTimeout(() => {
        this.hasBackground = false
      }, 500)
    },
  },
})
